.App2 {
  text-align: center;
}

.App-header4 {
  background-color: #23fa60;
  overflow-y: hidden;
  overflow-x: hidden;
  min-height: 100vh;
  align-items: center;
  flex:1;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.logodrops{
  height:100px;
  align-items: center;
  justify-content: center;
  display: flex;
  width:100%;
  background-color: white;
}

.link{
  width:13%;
  border: 0;
  margin: 0;
  padding: 0;
  height: 7vw;
  background-color: blue;
  transform: translateY(0px);
  transform: rotateY(0px);
}

.imagem{
  border: 0;
  margin: 0;
  padding: 0;
  width:100%;
  height: 100%;
  background-color: blue;
  transform: translateY(0px);
  transform: rotateY(0px);
}

.espacos_dist{
  font-weight: bold;
  color: black;
  height: 50px;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.espacamentos_dist{
  overflow:hidden;
  height:300vw;
  width:100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slides{
  overflow:hidden;
  width: 100vw;
  height: 500px;
  font-weight:bold;
  background-color: beige;
}

.banner_preto{
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: #111425;
}

.img_main_dist{
  width:100%;
  height: 100%;
}

.livro{
  height: 500px;
  width: 100%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagina1{
  width: 100%;
  height: 100%;
  background: white;
}

.csss{
  overflow-x: hidden;
  height: 600px;
  background-color: white;
  width: 100%;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.nuvem{
  overflow-x: hidden;
  background-color: white;
  height: 40%;
  width: 100%;
  position:absolute;
  align-items: flex-start;
}

.nuvem4{
  overflow-x: hidden;
  width: 200px;
  height: 100px;
  left:-200px;
  animation: nuv 40s infinite;
}
.nuvem5{
  overflow-x: hidden;
  position: absolute;
  width: 200px;
  height: 100px;
  left:-200px;
  animation: nuv 200s infinite;
  animation-delay: 20s;
  top:100px
}

.sol{
  position: absolute;
  left:200px;
  width: 100px;
  height: 100px;
  animation: solzinho 60s linear infinite;
}

@keyframes solzinho {
  0%{
    transform: rotate(0deg);
  }
  0%{
    transform: rotate(360deg);
  }
}
.nuvem6{
  position: absolute;
  width: 200px;
  height: 100px;
  left:-200px;
  animation: nuv 150s infinite;
  animation-delay: 40s;
  top:100px
}

.nuvem7{
  position: absolute;
  width: 200px;
  height: 100px;
  left:-200px;
  animation: nuv 180s infinite;
  animation-delay: 60s;
  top:0px
}

.cidade{
  width: 100%;
  height:80%;
}

.calcada{
  height: 50%;
  width: 100%;
  background-color:#6a7394;
}
.calcada2{
  overflow:hidden;
  height: 30%;
  width: 100%;
  background-color:gray;
}
.cidades{
  overflow-x: hidden;
  position: relative;
  top:40%;
  width: 100%;
  height: 50%;
  display: flex;
}
.covvi{
  overflow-x: hidden;
  position: absolute;
  margin-top: -1%;
  width: 75px;
  height: 130px;
  border-radius: 25px;
  animation:  walk 1s steps(8) infinite, covvidan 20s linear infinite;
  background:url(https://caveofprogramming.com/images/capguy-walk.png);
}

.covvi2{
  overflow-x: hidden;
  width: 75px;
  margin-left: 2000px;
  height: 130px;
  border-radius: 25px;
  animation:  walk2 1s steps(8) infinite, covvidan2 20s linear infinite;
  background:url(https://caveofprogramming.com/images/capguy-walk.png);  margin-top: -20%;
}
@keyframes walk {
  0%{
    background-position: 0px;

  }
    100%{
    background-position: 3000px;
  }
}

@keyframes covvidan {
  0%{
    transform:translateX(-500px);

  }
  100%{
    transform: translateX(1300px);
  }
}
@keyframes nuv {
  0%{
    transform: translateX(-1000px);
  }
 100%{
    transform: translateX(1000px);
  }
}

@keyframes covvidan2 {
  0%{
    transform:translateX(4000px);
    transform: scaleX(-1);
  }
  100%{
    transform: translateX(-500px);
  }
}

@keyframes walk2 {
  0%{
    background-position: 0px
  }
    100%{
    background-position: 3000px
  }
}

.carro{
  width:300;
  height: 150px;
  animation: carrinho 10s linear infinite;
  margin-top:10%;
}

@keyframes carrinho {
  0%{
    transform:translateX(-1000px);
  }
  100%{
    transform:translateX(1000px);
  }
}