.App{
    text-align: center;
  }

  .App-header {
    background-color: #23fa60;
    overflow-y: hidden;
    overflow-x: hidden;
    min-height: 100vh;
    align-items: center;
    flex:1;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .link{
    width:13%;
    border: 0;
    margin: 0;
    padding: 0;
    height: 7vw;
    background-color: white;
    transform: translateY(0px);
    transform: rotateY(0px);
  }

  .imagem{
    width:26%;
    border: 0;
    margin: 0;
    padding: 0;
    height: 7vw;
    background-color: white;
    transform: translateY(0px);
    transform: rotateY(0px);
  }

  .book{
    height: 600px;
    width: 600px;
  }

  .logodrops{
    height:100px;
    align-items: center;
    justify-content: center;
    display: flex;
    width:100%;
    background-color: white;
  } 

  .p1{
    color: white;
    font-weight: bold;
    font-size: 6vw;
  }
  .p2{
    color: white;
    font-weight: bold;
    width:100%;
    height: 400px;
    font-size: 2vw;
    background-color: black;
    border:0;
    padding: 0;
    margin: 0;
  }
  .p3{
    color: white;
    font-weight: bold;
    font-size: 9.4vw;
  }

  .textossobre{
    height: 100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #23fa60;
  }
  .SobreDrops{
    justify-content: center;
    align-items: center;
    height:100%;
    flex-direction: column;
    display: flex;
    font-weight: 500;
    width:80%;
    color: black;
    font-weight: bold;
    background-color:#23fa60;
    white-space: pre-line;
    font-size: 1.7vw;
  }

  @keyframes coresdrps {
    0%{
      background-color:#3751e0;
    }
    50%{
      background-color:#23fa60;
    }
    100%{
      background-color: #3751e0;
    }
  }
  
  @keyframes troc {
    0%{
      background-color:blue;
    }
    50%{
      background-color:red;
    }
    100%{
      background-color:blue;
    }
  }
  
  .contatos{
    overflow:hidden;
    margin-top: 100px;
    flex-direction: column;
    display: flex;
    align-items: center;
    height:3000;
    width: 100%;
    background-color:white;
    margin-top: 10px;
  }

  .conheca{
    height:100px;
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    color:black;
    font-weight: bold;
  }

  .scene{
    display:flex;
    justify-content: space-around;
    flex-direction: row;
    position: relative;
    width: 100%;
    height: 500px;
    background:#23fa60;
    background-attachment: fixed;
    overflow: hidden;
  }
  
  .scene i{
    position: absolute;
    background: #fff;
    border-radius: 50%;
    animation: animate linear infinite;
  
  }
  
  @keyframes animate {
    0%{
      opacity: 0;
      transform:translateY(0);
  
    }
    10%,99%{
      opacity: 1;
      
    }
    100%{
      opacity: 0;
      transform: translateY(-100px);
    }
    
  }
  
  .alunos{
    flex-direction: column;
    display: flex;
    width:44%;
    height:100%;
    
    justify-content: space-around;
    }

    .todosalunos{
        display: flex;
        height: 10%;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        flex-direction:row;
        }

    
.nomeal{
    display: flex;
    font-size: 18px;
    width:80%;
    margin-left: 10px;
    color:black;
    font-weight: bold;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
  }
  .fotosalunos{
    background-color: white;
    width:60px;
    height:60px;
    border-radius:10px;
  }

  .pai_p1{
    height: 20%;
    width: 100%;
  }
  .pai_p2{
    height: 20%;
    width: 100%;
    font-size: 2.4vw;
  }
  .pai_p3{
    height: 20%;
    width: 100%;
    font-weight: bold;
    font-size: 2.4vw;
  }

  .fontt{
    font-weight: bold;
    font-size: 2vw;
    color: black;
  }

  .imgs_p1{
    width: 100%;
    height: 500px;
  }

