.App2 {
    text-align: center;
  }

  .App-header {
    background-color: #23fa60;
    overflow-y: hidden;
    overflow-x: hidden;
    min-height: 100vh;
    align-items: center;
    flex:1;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .logodrops{
    height:100px;
    align-items: center;
    justify-content: center;
    display: flex;
    width:100%;
    background-color: white;
  }

  .link{
    width:13%;
    border: 0;
    margin: 0;
    padding: 0;
    height: 7vw;
    background-color: blue;
    transform: translateY(0px);
    transform: rotateY(0px);
  }

  .imagem{
    border: 0;
    margin: 0;
    padding: 0;
    width:100%;
    height: 100%;
    background-color: blue;
    transform: translateY(0px);
    transform: rotateY(0px);
  }
  
  .espaco{
    overflow:hidden;
    font-weight: bold;
    color: black;
    height: 100px;
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .espacamento{
    height:500px;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .imgs{
    display: flex;
    height: 100%;
    width: 40%;
    background-color: red;
  }
  .slides{
    display: flex;
    flex-direction: row;
    width: 60vw;
    height: 400px;
    background-color: beige;
    color: red;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .banner_preto{
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    background-color: #111425;
  }

  .espaco3{
    height: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @keyframes slide_t {
    0%{
      background-color: aquamarine;
    }
    40%{
      background-color: white;
    
    }
    80%{
      background-color: aquamarine;
    }
    100%{
      background-color: white;
    }
  }

  .text{
    display: flex;
    width: 60%;
    font-size: 1.5vw;
    word-wrap:break-word;
    white-space: pre-line;
    text-align: center;
    font-weight: bold;
  }
  .botao{
    width: 100px;
    height: 100px;
    border-radius: 50px;
    background-color: red;
  }
  .botao:hover{
    animation: opacity_button 10s;
  }
  .botao:not(:hover){
    opacity: 0;
}
  @keyframes opacity_button {
    0%{
      opacity: 0;
    }
    10%{
      opacity: 0.0;
    }
    20%{
      opacity:0.0;
    }
    30%{
      opacity:0.3;
    }
    40%{
      opacity:0.4;
    }
    50%{
      opacity:0.5;
    }
    60%{
      opacity:0.6;
    }
    70%{
      opacity:0.7;
    }
    80%{
      opacity:0.8;
    }
    90%{
      opacity:0.9;
    }
    100%{
      opacity: 1;
    }
  }