.div_main{
    width: 100vw;
    height:200vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: aqua;  
    overflow-x: hidden; 
}

.logo_redes{
height: 5%;
width: 100%;
background-color: white;
display: flex;
justify-content:center;
align-items:center;
}

.botao_redes{
    padding: 0;
    margin: 0;
    border: 0;
    width:16%;
    height: 90%;
    display:flex;
}

.img_redes_botao{
    height: 100%;
    width: 100%;
}
.titulo_redes{
    height: 5%;
    width:  100%;
    background-color: green;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3vw;
    font-weight: bold;
    color:white;

}
.public_redes{
    display: flex;
    justify-content: flex-start;flex-direction: column;
    align-items: center;
    height: 90%;
    color:black;
    font-size: 3vw;
    width:  100%;
    background-color: red;
    overflow:hidden;
}

.postagem{
    overflow:hidden;
    margin-top:1px;
    width: 70%;
    height: 24%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: blue;
}