.App{
    text-align: center;
    background-color: #2fed7e;
    color:black;
    font-size: 20px;
    font-weight:bold;
  }

  .App-header {
    background-color: #2fed7e;
    flex-direction: column;
    overflow-y: hidden;
    overflow-x: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    align-items: center;
    flex:1;
    font-size: calc(10px + 2vmin);
    color: white;
  }


  .App-header_10 {
    overflow-y: hidden;
    background-color: #23fa60;
    flex-direction: column;
    overflow-y: hidden;
    overflow-x: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    align-items: center;
    flex:1;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  .central{
    overflow: hidden;
    width: 200px;
    align-items: center;
    justify-content: center;
    height: 200px;
    display: inline-block;
    position: absolute;
  }
  .central2{
    overflow: hidden;
    width: 200px;
    align-items: center;
    justify-content: center;
    background-color:red;
    height: 200px;
    display: inline-block;
    position: relative;
  }
  .central span:nth-child(1){
    position: absolute;
    left: 0;
    width: 400%;
    height: 5px;
    background:blue;
    animation: cobra1 4s linear infinite;
    animation-delay:0.5;
    opacity: 0;
  }
  


  @keyframes cobra1 {
    0%{
        transform: translateX(-200%);
        opacity: 1;
    }
    
    100%{
        
        transform: translateX(200%);
        opacity: 1;
        
    }
  }
  


  .central span:nth-child(2){
    position: absolute;
    top: 0;
    right: 0;
    width: 5px;
    height: 400%;
    background:blue;
    animation: cobra2 4s linear infinite;
    animation-delay: 0.25s;
    opacity: 0;
}

@keyframes cobra2 {
    0%{
        transform: translateY(-200%);
        opacity: 1;
    }
    
    100%{
        transform: translateY(200%);
        opacity: 1;
    }
  }

  .central span:nth-child(3){
    position: absolute;
    bottom: 0;
    left: 0;
    width: 400%;
    height: 5px;
    background:blue;
    animation: cobra3 4s linear infinite;
    animation-delay:-0.25s;
    opacity: 1;
}

@keyframes cobra3 {
    0%{
        transform: translateX(200%);
        opacity: 1;
    }
    
    100%{
        transform: translateX(-200%);
        opacity: 1;
    }
  }

  .central span:nth-child(4){
    position: absolute;
    bottom: 0;
    left: 0;
    width: 5px;
    height: 400%;
    background:blue;
    animation: cobra4 4s linear infinite;
    animation-delay: 0.75s;
    opacity: 0;
}

@keyframes cobra4 {
    0%{
        transform: translateY(200%);
        opacity: 1;
    }
    
    100%{
        transform: translateY(-200%);
        opacity: 1;
    }
  }

  .img{
      width: 100%;
      height: 100%; 
      background-color: white;
      position:relative;

  }

  @keyframes sumir {
    0%{
        transform: translateY(100%);
        animation-delay: 2s;
        opacity: 0;
    }
    10%{
        transform: translateY(0%);
        animation-delay: 2s;
        opacity: 1;
    }
    33%{
        transform: translateY(0%);
        animation-delay: 2s;
        opacity: 1;
    }
    90%{
        transform: translateY(0%);
        animation-delay: 2s;
        opacity: 1;
    }

    100%{
        transform: translateY(-200%);;
        
    }
  }

  .h1{
      color:white;
      opacity: 1;
      width: 200px;
      height: 200px;
      position: absolute;
      background-color: yellow;
      font-size: 20px;
      animation: sumir 2.38s infinite;
      animation-delay: 0.7s;
      opacity: 0;
  }

  @keyframes h {
      0%{
          opacity: 1;
      }
      50%{
        opacity: 0;
      }
      100%{
          opacity: 1;
      }
  }


  .central2 span:nth-child(1){
      top:0;
      left:0;
      background-color: black;
      width: 100%;
      height: 10px;
      position:absolute;
      animation: centspan2 4s linear infinite;
  }

  @keyframes centspan2 {
      0%{
          transform: translateX(100%);
      }
      24.9%{
        transform:rotate(0deg);
      }
      25%{
        transform:rotate(90deg);
        transform: translateY(100%);          

    }
  }

  .lab{
    width:100%;
    height: 600px;
  }

