.dados{
width: 100%;
height: 100vw;
background-color: rgb(203, 203, 235);
overflow-x: hidden;
overflow-y:hidden;
}
.header{
    flex-direction:row;
    display: flex;
    height: 10%;
    align-items: center;
    justify-content: space-around;
    color: black;
    font-size: 2vw;
    font-weight: bold;
    width: 100%;
    background-color: white;
}
.bandeira{
    width: 16%;
    height: 90%;
}
.nome{
    width: 58%;
    height: 100%;
    justify-content: center;
    display:flex;
    align-items: center;
}
.outros{
    width: 20%;
    height: 100%;
}

.painel{
    height: 14%;
    margin-left: 10px;
    align-items: center;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
.atualizacao{
    align-items: center;
    height: 80%;
    width:30%;
    border-radius: 6px;
    background: white;
    display:flex;
    font-size: 1vw;
    color: black;
    flex-direction: column;
    justify-content: space-around;
}

.att_a1{
    color:black;
    font-weight: bold;
    font-size: 1.5vw;
}
.att_a2{
    color:black;
    font-weight: bold;
    font-size: 3vw;
}
.att_a3{
    color:black;
    font-weight: bold;
    font-size: 1.5vw;
}

.namegraph{
    color:black;
    font-weight:bold;
    font-size: 3vw;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-top:10px;
    height: 10%;
    width: 100%;
    background: white;
}
.graficos{
    overflow:hidden;
    height: 40%;
    width: 100%;
    background: white;
    overflow:hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.graficos1:hover{
    opacity: 1;
}
.graficos2:hover{
    opacity: 1;
}
.graficos3:hover{
    opacity: 1;
}

.graficos1{
    opacity: 0.8;
    display: inline-block;
    overflow:hidden;
    position: relative;
    height: 90%;
    width: 30%;
    border-radius: 8px;
    background-color: rgb(235, 230, 224);
    background: url(https://www.camara.leg.br/midias/image/2020/10/img20201022140139807-768x512.jpg);
    border:2px;
    border-color: blue;
}
.graficos2{
    opacity: 0.8;
    display: inline-block;
    overflow:hidden;
    position: relative;
    height: 100%;
    width: 34%;
    border-radius: 8px;
    background-color: rgb(235, 230, 224);
    border:2px;
    border-color: blue;
}
.graficos3{
    opacity: 0.8;
    position: relative;
    overflow:hidden;
    display: inline-block;
    height: 90%;
    width: 30%;
    border-radius: 8px;
    background-color: rgb(235, 230, 224);
    border:2px;
    border-color: blue;
}

.graficos1 span:nth-child(1){
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: blue;
    top:0;
    left: 0;
    animation: result 3s infinite;   
}
.graficos1 span:nth-child(2){
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: blue;
    top:0;
    right: 0;
    animation: result 3s infinite;   
}
.graficos2 span:nth-child(1){
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: blue;
    top:0;
    left: 0;
    animation: result 3s infinite;
}

.graficos3 span:nth-child(1){
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: blue;
    top:0;
    left: 0;
    animation: result 3s infinite;
    
}
.graficos2 span:nth-child(2){
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: blue;
    top:0;
    right: 0;
    animation: result 3s infinite;
}

.graficos3 span:nth-child(2){
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: blue;
    top:0;
    right: 0;
    animation: result 3s infinite;
    
}
@keyframes result {
    0%{
        transform: translateY(0%);
    }
    0%{
        transform: translateY(-100%);
    }
}

