.confiaveis_conf{
    width:100vw;
    flex-direction: column;
    background-color: #23fa60;
    align-items: center;
    overflow:hidden;
}

.header_conf{
    height: 20vh;
    background-color: white;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.botao_logo_conf{
width:12%;
height:100%;
padding:0;
border:0;
margin: 0;
background: white;
}

.imagem_logo_conf{
    width:70%;
    height: 80%;
}

.titulo_conf{
    height: 10vh;
    width: 100%;
    background-color:black;
    justify-content: center;
    display: flex;
    align-items: center;
    color:white;
    font-weight: bold;
    overflow: hidden;
    font-size: 2vw;
}

.public_conf{
    height: 100vh;
    width:100%;
    background: #23fa60;
    overflow: hidden;
    
}