
.main_js{
    width: 100vw;
    height: 140vw;
    overflow-x:hidden;
    background-color: #2fed7e;
}

.app_iniciojs{
    width:100%;
    height: 1%;
    background-color: blue;
    justify-content: center;
    display: flex;
}

.estilo_iniciojs{
    width: 100%;
    height: 100%;
}

.logo_iniciojs{
    height: 6%;
    width:100%;
    background-color: white;
    align-items: center;
    justify-content: center;
    display: flex;
    overflow:hidden;
}

.banner_iniciojs{
    margin-top: 0.5%;
    flex-direction: column;
    width:100%;
    display:inline-block;
    overflow:hidden;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 30%;
    background-color: black;
}

.banner_redirecionamentojs{
    margin-top: 0.5%;
    width:100%;
    height: 30%;
    background-color: white;
    }               

.catalogo_js{
    height:32%;
    width: 100%;
    background-color: black;
    margin-top: 0.5%;
}

.img_banner_iniciojs{
    position: relative;
    overflow:hidden;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.img_banner_planetajs{
    width: 420px;
    height: 400px;
    position: relative;
    top:-84%;
    left:32%;
    border-radius: 200px;
}

.img_banner_planetajs:hover{
    animation: rotacionar 200s infinite;
  }

@keyframes rotacionar {
    from{
        transform: rotate(0deg);
    }
    to{
      transform: rotate(360deg);
    } 
  }