.App2 {
    text-align: center;
  }

  .App-header {
    background-color: #23fa60;
    overflow-y: hidden;
    overflow-x: hidden;
    min-height: 100vh;
    align-items: center;
    flex:1;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .logodrops{
    height:100px;
    align-items: center;
    justify-content: center;
    display: flex;
    width:100%;
    background-color: white;
  }

  .link{
    width:13%;
    border: 0;
    margin: 0;
    padding: 0;
    height: 7vw;
    background-color: blue;
    transform: translateY(0px);
    transform: rotateY(0px);
  }

  .imagem{
    border: 0;
    margin: 0;
    padding: 0;
    width:100%;
    height: 100%;
    background-color: blue;
    transform: translateY(0px);
    transform: rotateY(0px);
  }
  
.testes{
  height:400px;
  width:100%;
  background-color: black;
}
.span{
  height: 100px;
  width: 600px;
  background-color: blanchedalmond;
}

.ceu{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 600px;
  width: 100%;
  background-color: black;
  color: aliceblue;
}

.covid{
  position: relative;
  display: inline-block;
  padding: 150px 150px;
  color: white;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-decoration: none;
  font-size: 24px;
  overflow: hidden;
  transition: 0.2s;
  transition-delay: 1s;
}

.covid:hover{
  color: #255784;
  background-color: #2196f3;
  box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
  transition-delay: 2s;

}

.covid span{
  position: absolute;
  display: block;
}

.covid span:nth-child(1){
  top:0;
  left: -100%;
  width: 100%;
  height: 2px;
  background:linear-gradient(90deg, transparent,#2196f3 );
}

.covid:hover span:nth-child(1){
  left:100%;
  transition:1s;
}

.covid span:nth-child(1){
  top:0;
  left: -100%;
  width: 100%;
  height: 2px;
  background:linear-gradient(90deg, transparent,#2196f3 );
}

.covid:hover span:nth-child(1){
  left:100%;
  transition:1s;
}
.covid span:nth-child(2){
  top:-100%;
  right:0;
  width: 2px;
  height: 100%;
  background:linear-gradient(180deg, transparent,#2196f3 );
}

.covid:hover span:nth-child(2){
  top:100%;
  transition:1s;
  transition-delay: 0.25s;
}
.covid span:nth-child(3){
  bottom:0;
  right: -100%;
  width: 100%;
  height: 2px;
  background:linear-gradient(270deg, transparent,#2196f3 );
}

.covid:hover span:nth-child(3){
  right:100%;
  transition:1s;
  transition-delay: 0.5s;
}

.covid span:nth-child(4){
  bottom:-100%;
  left:0;
  width: 2px;
  height: 100%;
  background:linear-gradient(360deg, transparent,#2196f3 );
}

.covid:hover span:nth-child(4){
  bottom:100%;
  transition:1s;
  transition-delay: 0.75s;
}

.covids{
  height: 100px;
  width: 100px;
  background-color: honeydew;
}
.covids:hover{
  animation: ir 1s infinite;
}

@keyframes ir {
  0%{
  top:0;
  left: -100%;
  width: 100%;
  height: 2px;
  background:linear-gradient(90deg, transparent,#2196f3 );
  }
  25%{
  top:-100%;
  right:0;
  width: 2px;
  height: 100%;
  background:linear-gradient(180deg, transparent,#2196f3 );
  }
  50%{
  bottom:0;
  right: -100%;
  width: 100%;
  height: 2px;
  background:linear-gradient(270deg, transparent,#2196f3 );
  }
  75%{
  bottom:-100%;
  left:0;
  width: 2px;
  height: 100%;
  background:linear-gradient(270deg, transparent,#2196f3 );
  }
  100%{
  top:0;
  left: -100%;
  width: 100%;
  height: 2px;
  background:linear-gradient(90deg, transparent,#2196f3 );
  }
}
