.install_app{
    height: 100vh;
    width: 100vw;
    background: #23fa60;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    display: flex;
}

.img_install{
    width: 80%;
    height: 80%;
    background-color: white;
    border-radius: 10px;
    padding: 0;
    margin: 0;
    border:0;
}
.imagem_install{
    background-color: #23fa60;
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px;
    border-radius: 10px;
    display: flex;
}
.botao_install{
    margin-top: 100px;
    display: flex;
    height: 100px;
    border-radius: 10px;
    width: 24vw;
    padding: 0;
    margin: 0;
    display: flex;
    border:0;
    background: black;
}

.botao_install:hover{
    animation: cores_inst 4s;
}

.a_install{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration:none;
    font-size: 1.4vw;
    font-weight: bold;
    color:white;
}

.botao_voltar_init{
    width: 16vw;
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: white;
    border:0;
    margin:0;
}

.img_voltar_init{
    width: 50%;
    height: 100%;
}

.voltar_init{
    display: flex;
    justify-content:center;
    align-items: center;
    margin-top: -60px;
    height: 100px;
    width: 100vw;
    background-color: white;
}
@keyframes cores_inst {
    0%{
        background: green;
    }
    100%{
        background:blue;
    
}}